.ellipsis-text-company-intro

{
    
    white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 550px;
    
}




.svg-lib
{
    
    width: 85px;
}





/* Zoom effect */
.card .hover01 svg {
	-webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}
.card .hover01:hover svg {
	-webkit-transform: scale(1.1);
	transform: scale(1.1);
}
