@charset "UTF-8";

.table-card
{

height: 100%;

}


.table-card-block
{
    
    
            //overflow: auto;
    overflow: hidden;
}


.w-pg-selector {
    width: 130px;
}



.table-card .card-body {
    padding: 0;
    height: calc(100% - 66px - 62px);
    overflow: auto;
}


.filter-bg
{
    
    background-color: #f5f7fa;
    border: solid 1px #cacfd4;
}





.table-bordered {
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
}

.table-card .card-body
{
    
    padding: 0;
    height: 100%;
    overflow: auto;
}

.table-card .table
{
    
  
     border-collapse: collapse;
    border-radius: 0.3rem;
   // overflow: hidden;
    
    tbody
    {
        
        overflow: auto;
        height: 100%;
    }
    
    
}


.table-card h5
    {
        
        line-height: 1.8;
    }




.table thead tr th, table thead tr th {
    
   
    position: sticky;
    top: -1px;
    z-index: 99;
    background-color: $body-color;
   border: 0.0625rem solid #f2f2f2 !important;
  
   
}

table tbody tr td, .table tbody tr td
{
    
    border-top: 0.03125rem solid #f2f2f2 !important;

padding: 0.625rem 1.25rem ;
}







.table th
{
    
    border-top: 0.03125rem solid #f2f2f2 !important;

padding: 0.625rem 1.25rem ;
}









.dataTables_wrapper
{
    
    margin-left: 15px;
    margin-right: 15px;
   
}



.nav-pagination {
    justify-content: space-between;
}

.nav-pagination .table-info {
    display: flex;
}



.nav-pagination .table-pagination .dataTables_paginate {
    display: flex;
    margin: 0;
    align-items: center;
}

.table-card-footer
{
    
    display: flex;
align-items: center;
justify-content: space-between;
}


.table-card-footer .nav-pagination
{
    
    display: flex;

width: 100%;
}


.table-card-footer .table-entries label

{
    
    display: flex;
}

.table-card-footer select
{
    margin-left: 5px; 
    margin-right: 5px;
}







table thead th[data-type="numeric"], table tbody td[data-type="numeric"], .table thead th[data-type="numeric"], .table tbody td[data-type="numeric"]
{
    
    
    text-align: right;
    
}





table thead th[data-type="datetime"], table tbody td[data-type="datetime"], .table thead th[data-type="datetime"], .table tbody td[data-type="datetime"]
{
    
    text-align: right;
    
}



table thead th[data-type="badge"], table tbody td[data-type="badge"], .table thead th[data-type="badge"], .table tbody td[data-type="badge"] {

 
    text-align: center;

}



table .badge-danger {

    color: #e53e3e;

background:   #fdf2f2;
font-weight: bold;
}




table .badge-success {

color:#49bc78;
font-weight: bold;
background:#ddf3e6;
    
}





table .badge-warning {

color:#fb8c00;
font-weight: bold;
background:#ffebd1fc;
    
}



.table-row{
cursor:pointer;
}





.table th, .table td
{
    
    
    vertical-align: middle;
}











