@charset "UTF-8";




.wrapper-active-jobs
{
    
    
    height: calc(100vh - 240px);
    overflow-x: hidden;
    overflow-y: auto;
}





.scroll-wrapper-dashboard {
    position: relative;
    height: calc(100vh - 122px);
    overflow: auto;
}



.wrapper-all-jobs
{
    
    
 
    height: 182px;
    overflow-x: hidden;
    overflow-y: auto;
    margin-bottom: 3px;
}



.wrapper-all-recent
{
    
    
 
    height: 225px;
    overflow-x: hidden;
    overflow-y: auto;
}



.no-card-unset .card
{
    
    box-shadow: none;
    border: 1px solid rgba(0, 0, 0, 0.04);
    
}



.wrapper-all-recent .avatar
{
    
    width: 18px;
    height: 18px;
}


.scroll-wrapper-dashboard .progress 
{
    
    margin-bottom: 6px;
}











