@charset "UTF-8";


.card-deck {
  margin: 0 -5px;
  justify-content: space-between;
}

.card-deck .card {
  margin: 0 0 1rem;
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .card-deck .card {
    -ms-flex: 0 0 48.7%;
    flex: 0 0 48.7%;
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .card-deck .card {
    -ms-flex: 0 0 32%;
    flex: 0 0 32%;
  }
}

@media (min-width: 992px)
{
  .card-deck .card {
    -ms-flex: 0 0 24%;
    flex: 0 0 24%;
  }
}


.split-view {
    padding: 0;
    display: flex;
    flex-direction: row;
    height: 100%;
    
}


@media (min-width: 768px)
    
{
.side-section {
    height: auto;
    min-height: 3.75rem;
    max-width: 21.875rem;
}
.side-section {
    border-right: 0.0625rem solid #f2f2f2;
    padding: 0;
    overflow: auto;
}


}



.content-section.no-shadow {
    box-shadow: none;
}








.content-section {
    padding: 0;
    height: 100%;
    overflow: auto;
}







.side-section .filter-block {
    height: 100%;
}












