@charset "UTF-8";


.scroll-wrapper-interview {
    position: relative;
    height: calc(100vh - 108px);
    overflow: auto;
}





.video-container


{
    
    border: 1px solid #ddd !important;
    width: 100%;
    height: calc(100vh - 125px);
    
     display: flex;
  justify-content: center;
  align-items: center;
}



.permission-table table tbody tr td, .permission-table.table tbody tr td {
    border-top: none !important;
   
}



.video-thumb-interview
{
    
    width: 400px;
    height: 250px;
      border: 1px solid #ddd !important;
}





.sticky-panel-video-interview {
    align-self: flex-start;
    height: auto;
    position: sticky;
    top: 0px;
}






















