  @media (min-width: 768px) {
    .sidebar {
      display: block; } }
  .sidebar .icon svg {
    fill: #b3b1b1; }
  .sidebar.expand {
    display: block;
    width: 13.3rem;
    /*   @extend .shadow;*/
    /* .footer {
            display: flex;
        }*/ }
    .sidebar.expand .nested > a:after {
      content: url("../img/arrow-down.svg");
      width: 1.5625rem;
      height: 0.875rem;
      color: #868e96;
      position: absolute;
      right: 0.9375rem;
      top: 0.9375rem;
      transition: 0.3s ease-in-out; }
  .sidebar ul {
    list-style-type: none;
    padding: 0 0 3.75rem 0;
    margin: 0;
    height: calc(100% - 60px); }
    .sidebar ul:hover {
      overflow: hidden; }
    .sidebar ul.sticky {
      position: sticky;
      top: 60px; }
    .sidebar ul a {
      text-decoration: none;
      color: #000; }
    .sidebar ul li {
      min-height: 2.8125rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      border-left: 0.3125rem solid transparent; }
      .sidebar ul li.open {
        background: #f5f7fa; }
        .sidebar ul li.open > a:after {
          transform: rotate(180deg); }
      .sidebar ul li.active, .sidebar ul li:hover {
        border-left: 0.3125rem solid var(--secondary);
        background: #f5f7fa; }
        .sidebar ul li.active > a .icon svg, .sidebar ul li:hover > a .icon svg {
          fill: var(--secondary); }
      .sidebar ul li a {
        width: 100%;
        height: 100%;
        min-height: 2.8125rem;
        display: flex;
        align-items: center;
        word-break: break-word;
        position: relative;
        text-transform: capitalize; }
        .sidebar ul li a .icon {
          margin-right: 0.5375rem; }
          .sidebar ul li a .icon svg {
            /*   margin-top: px2rem(-2.5);*/
            margin-left: 4px; }
        .sidebar ul li a.notification .icon:after {
          content: "";
          display: inline-block;
          position: absolute;
          top: 0.46875rem;
          right: 0.46875rem;
          width: 0.5rem;
          height: 0.5rem;
          background: #e2445c;
          border-radius: 50%; }
      .sidebar ul li ul {
        display: none;
        margin: 0;
        padding: 0;
        height: auto;
        margin-left: -0.3125rem; }
        .sidebar ul li ul.visible {
          display: block;
          background: #f5f7fa; }
          .sidebar ul li ul.visible li {
            padding-left: calc(2.5rem + 0.9375rem); }
        .sidebar ul li ul li {
          margin: 0;
          padding: 0;
          border-left: 0.3125rem solid transparent; }
  .sidebar:not(:hover) .visible {
    display: none; }
  .sidebar.expand .visible {
    display: block; }
  .sidebar.expand a.notification .icon:after {
    top: 0.9375rem;
    right: 1.1rem; }
  .sidebar:not(.expand) a {
    font-size: 0px; }
  .sidebar:hover a.notification .icon:after {
    top: 0.9375rem;
    right: 1.1rem; }

