.tag-panel {
  padding: 0.25rem;
  margin-top: 1.25rem;
  border: 1px solid #eee;
  border-radius: .25rem; }

.calender-strip-br {
  border-left: 0.3rem solid var(--primary) !important; }

.media-panel {
  padding: 0.25rem;
  min-height: auto;
  margin-bottom: 1.25rem;
  border: 1px solid #eee;
  border-radius: .25rem; }

/*image question*/
.qs-img-wrapper {
  position: relative;
  width: 75px; }

.image-qs {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: .5s ease;
  backface-visibility: hidden; }

.middle {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  text-align: center;
  padding: 5px; }

.qs-img-wrapper:hover .image {
  opacity: 0.3; }

.qs-img-wrapper:hover .middle {
  opacity: 1; }

.trash {
  background-color: #343a40;
  font-size: 16px;
  padding: 16px; }

/*image question*/
.nav-link.active, .nav-item.show .nav-link {
  border-top: 2px solid var(--primary) !important; }

.form-control {
  height: inherit; }

.bd-img-upload .modal-content {
  height: 700px;
  overflow: auto; }

.add-qs-btn:hover {
  border: 2px #bccdd7 dashed;
  background: #e9ecef; }

.category-creation {
  margin-bottom: 8rem; }

.chevron > a {
