  content: "\f078";
  width: 35px;
  display: block;
  font-weight: 900; }

.chevron > a[aria-expanded="true"]:after {
  font-family: "Font Awesome 5 Free";
  content: "\f077";
  font-weight: 900; }

.chevron {
  padding-top: 5px; }

.card-footer-height {
  height: 46px !important; }

.time-locked {
