  counter-reset: my-awesome-counter; }

.category-listgroup ol li {
  counter-increment: my-awesome-counter; }

.category-listgroup ol li::before {
  content: counter(my-awesome-counter) ". ";
  font-weight: bold; }

.cat-question-import {
  width: 50%; }

.stickyweightage {
