@charset "UTF-8";

.weightage-width
{

width: 60px !important;
      height: 30px;

}


.timelimit
{
    
    width: 60px !important;
    height: 30px;
}


.l-pt
{
    
    padding-top: 2px;
}



.weightage .form-control
{
    
    height: 30px;
}



.height-fix {
    min-height: 75px !important;
}






.form-builder-add {
    align-items: center;
    width: 100%;
    padding: 0.5rem 1.3rem;
    line-height: 2rem;
    font-weight: 600;
    border: 2px #c9d7df dashed;
    border-radius: 4px;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}



.dropdown-menu-add-new {
  left: 40% !important;
 
}



.form-control, .input-group-text
{
    
    background-color: $body-color;
}





.quantity::-webkit-inner-spin-button, 
.quantity::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.quantity input[type=number] {
    -moz-appearance:textfield;
}






.pt-custom

{
    padding-top: 0.15rem !important;
    
}



.add-qs-dropdown .dropdown-menu
{
    
    background-color: var(--secondary);
    
}

.add-qs-dropdown .dropdown-menu a
{
    
    color: #fff;
}

.add-qs-dropdown .dropdown-item:hover
{
    
    background-color: #3C546A;
    
}



.add-qs-dropdown .dropdown-divider

{
    
    border-top: 1px solid #4B4B4B;
    
    
    
}




.dropdown-custom .btn-outline-secondary:hover, .dropdown-custom .btn-outline-secondary:focus
{
    
     background-color: #f5f7fa;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25rem;
    color: #000;
    outline: none !important;
    box-shadow: none !important;
   
}

.dropdown-custom .dropdown-menu
{
    
    width: inherit;
}


.dropdown-custom .btn-outline-secondary

{
    
    border-color: transparent;
    color: #000;
    background-color: $body-color;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25rem;
     height: 38.8px !important;
}


.dropdown-custom .btn-outline-secondary:not(:disabled):not(.disabled):active, .btn-outline-secondary:not(:disabled):not(.disabled).active, .show > .btn-outline-secondary.dropdown-toggle

{
    
     background-color: $body-color;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25rem;
    color: #000;
    outline: none;
    
}

.custom-select
{
    
    background-color: $body-color;
}


.qs-selection
{
    
    font-size: 14px;
    border: none !important;
    
}



