@charset "UTF-8";

.cv-container
{
    
   width: 21cm;
    height: 40.5cm;
    margin: 0 auto;
    background-color: #fff;

}


.cv-container-preview
{
    
   width: 100%;
    height: 40.5cm;
    margin: 0 auto;
    background-color: #fff;

}










.pg-breaker
{
    
    page-break-after: always;
    padding: 15px;
}



@media print
{

.pg-breaker
{
    
    display: none;
}

    
    
    .cv-container-preview
{
    
   width: 23cm;
    height: 33.2cm;
   zoom:130%

}
    
    .cv-sidebar
    {
        
     height: 33.2cm !important;   
        
    }
    
    
        
}




.cv-sidebar
{
    
    width: 35%;
    height: 1529px;
    background-color: var(--secondary);
    padding: 15px;
    color: #fff;
    
}







.main-content-cv
{
    
    width: 65%;
    height: 1122px;
   
    padding: 15px; 
    
    
}




.cv-sidebar h6
{
    
    color: #A0D7F6;
    
}


.cv-sidebar .footer {

    position: absolute;
    bottom: 0;
    
    flex-direction: column;
    align-items: center;
    
    padding: 0.625rem 0;
   

}


.cv-sidebar .footer .logo-sm {

    width: 5rem;

}



.cv-container textarea {
   resize: none;
}


.cv-container textarea {
    width: 100%;
    height: 170px;
}


.main-content-cv label
{
    
    color: var(--secondary);
}

.cv-hr
{
    
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}





.icon-drag svg
{
    
    width: 12px;
    height: 35px;
    margin-right: 5px;
    margin-bottom: 3px;
}




.profile-wrapper {
    position: relative;
    height: calc(100% - 0px);
    overflow: auto;
}







