@charset "UTF-8";

.job-manager
{
    
    padding: 0.3125rem;
}



.job-form-builder p.primary {
    margin-bottom: 0.3125rem;
    font-size: 1.25rem;
    font-weight: 900;
    color: var(--secondary);

}

.job-form-builder p.secondary {
    font-size: 0.84375rem;
    font-weight: 300;
    color: #6c757d;
}


.job-form-builder {
  position: relative;
  height: calc(100vh - 0.75rem - 1.8125rem - (1.9375rem * 4.3));
  overflow: auto;
}


.job-form-builder .custom-seprator
{
        border-right: 1px solid #dee2e6;
    
    
}






/*Vertical Job builder Steps*/


.list-group.vertical-steps .list-group-item{
  border:none;
  border-left:3px solid #bfbfbf;
  box-sizing:border-box;
  border-radius:0;
  counter-increment: step-counter;
  padding-left:20px;
  padding-right:0px;
  padding-bottom:20px;  
  padding-top:0px;
     background-color: transparent !important;
}
.list-group.vertical-steps .list-group-item.active{
  background-color:transparent;
  color:inherit;
     font-weight: 900; 
    
}
.list-group.vertical-steps .list-group-item:last-child{
  border-left:3px solid transparent;
  padding-bottom:0;
}
.list-group.vertical-steps .list-group-item::before {
  
  background-color:#fff;
  color:#555;
    
    border-radius: 50%;
border: 0.125rem solid #bfbfbf;
    
    
 content: "";
  display:inline-block;
  float:left;
  height:25px;
  line-height:22px;
  margin-left:-34px;
  text-align:center;  
  width:25px;  
}
.list-group.vertical-steps .list-group-item span,
.list-group.vertical-steps .list-group-item a{
  display:block;
  overflow:hidden;
  padding-top:2px;
}
/*Active/ Completed States*/
.list-group.vertical-steps .list-group-item.active::before{
  
    border-color: var(--primary);
font-family: "Font Awesome 5 Free";
content: "\f111";
font-size: 0.5rem;
    font-weight: 900; 
    color:var(--primary);
   
    width: 25px;
min-width: 25px;
height: 25px;
border-radius: 50%;
border: 0.125rem solid var(--primary);
    
    
  
}
.list-group.vertical-steps .list-group-item.completed{
  border-left:3px solid var(--primary);
    font-weight: 900;
}
.list-group.vertical-steps .list-group-item.completed::before{
  border-color: var(--primary);
font-family: "Font Awesome 5 Free";
content: "\f00c";
font-size: 0.5625rem;
    background: var(--primary);
    color: #fff;
}
.list-group.vertical-steps .list-group-item.completed:last-child{
  border-left:3px solid transparent;
}

/*Vertical Job builder Steps*/




.sticky-panel
{
    
    align-self: flex-start;

height: auto;

position: sticky;

top: 11px;
}





.breadcrumb__divider

{

display: flex;
    align-items: center;
    height: 55px;
font-size: 1.5rem;
    color: $dark;
    margin-left: 5px;
    margin-right: 5px;
}




.card-light-bg
{
    
   
    border-bottom: 1px solid rgba(0, 0, 0, 0.04);
    
}


.bg-hiring-cards-active
{
    
    
    
    border-color: var(--primary) !important;
    font-weight: bold;
}

.bg-hiring-cards-active a
{
    
    
   color: var(--primary) !important;
}




.card-hiring
{
    
    display: flex;
    flex: none;
    margin-bottom: 5px;
}



.card-hiring::after
{
    
    content: '';
    font-size: 25px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 9px;
    vertical-align: middle;
}


.hiring-cards .card-hiring:nth-last-child(1)::after
{
    
    display: none;
}








.scrolling-wrapper-flexbox {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  padding-bottom: 5px;  
  background-color: $body-color;
    padding: 10px;
}
.scrolling-wrapper-flexbox .card {
  flex: 0 0 auto;
  margin-bottom: 7px; 
}

.wrapper-hiring
{
    
    /* background-color: $body-color;*/
    padding: inherit;
    border: 1px solid rgba(0, 0, 0, 0.10);
    border-radius: 0.25rem;
    margin-bottom: 15px;
}




@media screen and (min-width: 767px) and (max-width: 1170px)
{ 

.job-form-builder .card-wrapper
{
    
    max-width: 75%;
}

}



.hiring-cards a
{
    color: $dark;
    
}




.hiring-cards .card-body
{
    
   
    padding: 0.6rem 0.5rem 0.5rem 0.5rem;
}




.ql-editor
{
    background-color: $body-color;
  
    border-radius: 0.25rem;
    
}



.hiring-cards .fa-times-circle
{
    
