.media-thumbnail-custom
{
    
    width: 100px;
    height: 100px;
    border-radius: 0.25rem ;
    
    cursor: pointer;
    object-fit: cover;
    border: 1px solid #dee2e6;
    padding: .25rem;
   margin: 5px 5px 25px 5px;
    
}






.imghover .overlay {
  width: 90%;
  height: 0%;
  
  text-align: center;
  position: absolute;
  left: 10px;
  bottom: 0px;
    top: 10px;
    right: 0px;
   
  transition: 1s ease;
        background: url("../img/zoom-img-hover.svg") ;
    
    background-size: cover;
   

}


.imghover:hover .overlay {
width: 90%;
    height: 90%;
    transition: 0.5s ease;
    cursor: pointer;
    left: 10px;
    top: 10px;
}




