/* Angular overides from dev team */

.table thead tr th .dropdown, table thead tr th .dropdown {
  position: absolute;
  top: 0;
  right: 0;
}
table thead th .dropdown-toggle::after{
  display: none;
}
.table thead tr th .icon svg, table thead tr th .icon svg{
  fill: #dbdbdb;
}
.table thead tr th .icon.active svg, table thead tr th .icon.active svg{
  fill: #a3a3a3;
}
.table .icon.small svg, .illustration.small svg{
  width: 1.5625rem;
  height: 1.5625rem;
}
.table .dropdown-menu{
 
  width: 14.375rem;
 
  margin-top: .625rem;
  z-index: 9970;
}
.dropdown-menu.filter li .button-group{
  display: flex;
  justify-content: flex-end;
  width: 100%;
}


dropdown-menu.filter ul.unstyled {
  list-style-type: none;
  padding: 0;
  border: 0.0625rem dotted #f2f2f2;
  max-height: 7.5rem;
  overflow: auto;
  background: var(--background-base);
}
table .dropdown-item:active {
  color: #000 !important;
  text-decoration: none !important;
  background-color: #f8f9fa !important;
}
.dropdown-menu.filter ul.unstyled li.sticky {
  position: sticky;
  top: 0;
  background: #f2f2f2;
  color: #737373;
  z-index: 9960;
}
.dropdown-menu.filter ul.unstyled li {
  min-height: 1.5625rem;
  border-bottom: 0.0625rem dotted #f2f2f2;
  padding: 0.3125rem;
}









