@charset "UTF-8";

$icon-hover-fill:  var(--secondary);




.sidebar {
    background: $sidebar-bg-color;
    border-right: $sidebar-border-right;
    width: $sidebar-compact-width;
    height: calc(100% - #{$topbar-height});
    position: fixed;
    left: 0;
    z-index: $zindex-2;
    display: none;
    top: 60px;  
    overflow: hidden;
    white-space: nowrap;
    
    
    
   transition:0.3s ease-in-out;

    @include media-breakpoint-up(md) {
        display: block;
    }
    
    
    
    
    .icon svg
    {
        
        fill: $icon-default-fill;
    }



    &.expand {
        display: block;
        width: $sidebar-width;
        
     /*   @extend .shadow;*/

        .nested {
            > a:after {
                content: url('../img/arrow-down.svg');
                width: px2rem(25);
                height: px2rem(14);
                color: $text-muted;
                position: absolute;
                right: $space-15;
                top: $space-15;
                transition: 0.3s ease-in-out;
                
            }
        }

       /* .footer {
            display: flex;
        }*/
    }


    ul {
        list-style-type: none;
        padding: 0 0 px2rem(60) 0;
        margin: 0;
        height: calc(100% - #{$topbar-height});

        &:hover {
           overflow: hidden;
        }

        &.sticky {
            position: sticky;
            top: $topbar-height;
        }


        a {
            text-decoration: none;
            color: $text-color;

        }

        li {
            min-height: px2rem(45);
            display: flex;
            flex-direction: column;
            justify-content: center;
            border-left: px2rem(5) solid transparent;
            // border-bottom: px2rem(1) dotted $gray-shine;

            &.open {
                background: $background-base;

                > a:after {
                    transform: rotate(180deg);
                }
            }

            &.active,
            &:hover {
                border-left: px2rem(5) solid var(--secondary);
                background: $background-base;

                > a {

                    .icon svg {
                       
                        fill: $icon-hover-fill;
                    }
                }
            }

            a {
                width: 100%;
                height: 100%;
                min-height: px2rem(45);
                display: flex;
                align-items: center;
                word-break: break-word;
                position: relative;
                text-transform: capitalize;

                .icon {
                    margin-right: $space-5;

                    svg {
                     /*   margin-top: px2rem(-2.5);*/
                    
    margin-left: 4px;
                    }
                }

                &.notification {
                    .icon {
                        &:after {
                            content: "";
                            display: inline-block;
                            position: absolute;
                            top: px2rem(7.5);
                            right: px2rem(7.5);
                            width: px2rem(8);
                            height: px2rem(8);
                            background: $danger;
                            border-radius: 50%;
                        }
                    }
                }
            }

            ul {
                display: none;
                margin: 0;
                padding: 0;
                height: auto;
                margin-left: px2rem(-5);

                &.visible {
                    display: block;
                    background: $background-base;

                    li {
                        padding-left: calc(#{$icon-width} + #{$space-15});
                    }
                }

                li {
                    margin: 0;
                    padding: 0;
                    border-left: px2rem(5) solid transparent;
                }
            }
        }
    }

  /*  .footer {
        position: absolute;
        bottom: 0;
        display: none;
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: $space-10 0;
        background: $sidebar-bg-color;

        p {
            width: 100%;
            text-align: center;
            margin-bottom: $space-5;
        }

        .logo-sm {
            width: px2rem(75);
        }
    }*/

    //Hover effect on links
  /*  &:hover {
        width: $sidebar-width;
       @extend .shadow;
      

        li {
            &.nested {
                > a {

                    &:after {
                        content: url('../img/arrow-down.svg');
                        width: px2rem(25);
                        height: px2rem(14);
                        color: $text-muted;
                        position: absolute;
                        right: $space-15;
                        top: $space-15;
                        transition: 0.3s ease-in-out;
                        
                    }
                }
            }

            a {
                font-size: $sidebar-font-size;
            }
        }

        .footer {
            display: flex;
        }

    }*/

    &:not(:hover) {
        .visible {
            display: none;
        }
    }

    &.expand {
        .visible {
            display: block;
        }

        a {
            &.notification {
                .icon {
                    &:after {
                        top: $space-15;
                        right: $space-25;
                    }
                }
            }
        }
    }

    &:not(.expand) {
        a {
            font-size: 0px;
        }

    }

    &:hover {
        a {
            &.notification {
                .icon {
                    &:after {
                        top: $space-15;
                        right: $space-25;
                    }
                }
            }
        }
    }

}









/*RIGHT SIDEBAR*/



.right-sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    /*z-index: 1002;*/
    top: 60px;
    right: 0;
    background-color: #fff;
    overflow-x: hidden;
    transition: 0.3s;
    padding-top: 60px;
    border-left: 0.0625rem solid #f2f2f2;
    white-space: nowrap;
}

.right-sidenav a {
    padding: 8px 8px 8px 16px;
    text-decoration: none;
    font-size: 1.1em;
    
    display: block;
    transition: 0.3s;
}

.right-sidenav a:hover {
    color: $secondary;
	
}

.right-sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 36px;
    margin-left: 50px;
}



@media screen and (max-height: 450px) {
  .right-sidenav {padding-top: 15px;}
  .right-sidenav a {font-size: 18px;}
}














