@charset "UTF-8";




.signup-container {
    display: flex;
    flex-direction: column;
    width: 85%;
    align-self: flex-start;
    
}


.login-container {
    display: flex;
    flex-direction: column;
    width: 500px;
    align-self: flex-start;
    
}


.plan-summary-card
{
    
    
    flex-shrink: 1;
    width: 50%;
    
    
}










@media screen and (min-width: 767px) and (max-width: 991px)
    
{
    
    .signup-container {
    
    
     width: 100%;
    }
    
    
    .plan-summary-card
{
      
    width: 75%;
}
    
    
    .plan-change-modal .card {
   
     
     width: 100% !important;
   margin-right: 15px;
         margin-left: 15px;
}

    
    
    
    
    

}



@media screen and (min-width: 992px) and (max-width: 1199px)
{
    
    
    
    
    
     .plan-change-modal .card {
   
     
     width: 45.99% !important;
         margin-right: 15px;
         margin-left: 15px;
   
}

    
    
    
    
    
    
    
    
    
    
}















.login .card {
    
    border-bottom-color: #1999E3 !important;
    border-bottom-width: 5px !important;
    
}

.login
{
   display: flex;
flex-direction: row;
justify-content: flex-end;
align-items: center;
 min-height: calc(100vh - 0px);
background-color: #f5f5f5;
/*border-top: 0.25rem solid var(--primary);*/
border-radius: 0;
 background-image: url(../img/body-bg.svg);   
    
    background-size: cover;    
}


.signup
{
    background-image: url(../img/hero.jpg);
    display: block;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: calc(100vh - 0px);
  

    
}



.border-r-flat
{
    border-top-right-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
    
}



.border-l-flat
{
    border-top-left-radius: 0px !important;
    border-bottom-left-radius: 0px !important;
    
}






.signup .card {
   
    border: 0px solid rgba(0, 0, 0, 0);
    border-bottom-color: #1999E3;
    border-bottom-width: 5px ;
    
}










.plan-summary-card .fa-check-circle
{
    
    font-size: 17px;
}



.plan-change-modal .card {
   
     border: 0.0625rem solid #ececec; 
     width: 22.280%;
  margin-right: 15px;
         margin-left: 15px;
}






.jobs-open-label
{
    
    font-size: 28px;
}


.login .illustration {
    display: flex;
    flex: 1;
    height: 100vh;
    padding-right: 1.5625rem;
   
  
   background-image: url(../img/hero.jpg);
}


.login .login-section {
    
    height: 100%;
   width: 50%;
    padding: calc(1.5625rem * 1);
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}



.underline
{
    
    text-decoration: underline !important;
}




.plan-selected
{
    
   border: 0.0625rem solid var(--primary) !important;  
    background-color: #fdfdfd; 
}




.plan-selected::after
{
    
   content: '\f058';
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    right: 18px;
    position: absolute;
    font-size: 16px;
    top: 18px;
    color: var(--primary);

}


.card-text-title
{
    
     font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    
    
}




.plan-selected .card-text-title
{
    
    color: var(--primary);
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5;
}







.btn-fix {
    padding: 0;
    border: none;
   
    white-space: normal;
  }





.plan-summary-card .footer
{
    
    position: absolute;
    bottom: 0;
    flex-direction: column;
    align-items: center;
    padding: 0.625rem 0;
    margin-right: 15px;
}


.plan-summary-card .footer img
{
    
    width: 32%;
}













