.interview-table table { width: 100%; box-sizing: border-box;  }

.interview-table th, .interview-table td { border: 1px solid #E9E9E9; box-sizing: border-box;  }

.interview-table th { text-transform: uppercase; font-size:10px; font-weight:700; padding: 10px 10px; color: #fff; background-color: $primary; letter-spacing: 1px;position: sticky;
    top: -1px;
    z-index: 99; }

.interview-table td { width: 14.285%; transition: all 0.3s; font-size: 14px;  font-weight: 400; font-size: 14px; padding: 1.5% 1.5% 5%; vertical-align: initial; padding: 0.5% 0 ; height: 125px; }

/*.day:hover { background-color: rgba(0,0,0,0.1); cursor:pointer; }*/

.today { color: #000; background-color: rgba(0, 0, 0, 0.08) !important; }




.interview-table span.number { margin-left: 10% }

.interview-table span.event { height: 4px; background-color: rgba(0,0,0,.3); display: block; margin: 5px 10%; border-radius: 2px; background-color: #91c33b; }

.interview-table span.event-multiday { margin: 5px -2px; border-radius: 0; }
.interview-table span.event-multiday-start { margin-right: -4px;  }
.interview-table span.event-multiday-finish { margin-left: -4px;  }

.interview-table span.event-ghost { background-color:transparent; }





