 
@charset "UTF-8";


:root {
  
  /*declare initial values of these variable for font color change calculation to work*/
  --redC: 0;
  --greenC: 0;
  --blueC: 0;
  /*the threshold at which colors are considered "light". 
    Range: decimals from 0 to 1, recommended 0.5 - 0.6*/
  --threshold: 0.5;
  /*checks the lightness of background*/
  --r: calc(var(--redC) * 0.2126);
  --g: calc(var(--greenC) * 0.7152);
  --b: calc(var(--blueC) * 0.0722);
  --sum: calc(var(--r) + var(--g) + var(--b));
  --perceived-lightness: calc(var(--sum) / 255);
  /*sets the color of text light or dark according to the calculated lightness*/
  --txt-color: hsl(0, 0%, calc((var(--perceived-lightness) - var(--threshold)) * -10000000%));
  /*this alt-color variable can be used anywhere we want the color to change as per the lightness
    or darkness of background color*/
}




$primary: var(--primary);
$secondary: var(--secondary);
$btn-text-color: var(--txt-color);




.bg-secondary {
    background-color: var(--secondary) !important;
}

.bg-primary {
    background-color: var(--primary) !important;
}




.btn-primary.disabled, .btn-primary:disabled {
   
    background-color: var(--primary);
    border-color: var(--primary);
}


.btn-outline-primary.disabled, .btn-outline-primary:disabled
{
    
     background-color: var(--primary);
    border-color: var(--primary);
}


.btn-outline-primary:not(:disabled):not(.disabled):active, .btn-outline-primary:not(:disabled):not(.disabled).active, .show > .btn-outline-primary.dropdown-toggle
{
    
    background-color: var(--primary);
    border-color: var(--primary);
    
}

.btn-outline-primary:focus, .btn-outline-primary.focus
{
     box-shadow: none;
    
}




.btn-primary
{
    
    background-color: var(--primary);
    border-color: var(--primary);
    color: $btn-text-color;
}

.btn-primary:hover, .btn-primary:active {
    background-color: var(--primary);
    background-image: linear-gradient(to top,rgba(0,0,0,.2),transparent 80%),linear-gradient(-180deg,var(--primary) 0,var(--primary) 90%);
    border-color: var(--primary);
     color: $btn-text-color;
}


.page-item.active .page-link
{
    
        background-color:  var(--primary);
    border-color:  var(--primary);
}

.btn-outline-primary {
    color: var(--primary);
    border-color: var(--primary);
}

.btn-outline-primary:hover {
    color: #fff;
    background-color: var(--primary);
    border-color: var(--primary);
}



.btn-primary.disabled, .btn-primary:disabled
{
    
    background-image: none;
     color: $btn-text-color;
}







a {
    color: var(--primary);
    
}

.badge-primary {
    
    background-color: var(--primary);
}



.progress-bar {
   
    background-color: var(--primary);
    
}



.custom-control-input:checked ~ .custom-control-label::before {
   
    border-color: var(--primary);
    background-color: var(--primary);
}


.text-primary {
    color: var(--primary) !important;
}




.btn-primary:not(:disabled):not(.disabled):active, .btn-primary:not(:disabled):not(.disabled).active, .show > .btn-primary.dropdown-toggle
{
    
    
        background-color: var(--primary);
    border-color: var(--primary);
     color: $btn-text-color;
}


.btn-primary:not(:disabled):not(.disabled):active:focus, .btn-primary:not(:disabled):not(.disabled).active:focus, .show > .btn-primary.dropdown-toggle:focus
{
    box-shadow: none;
    
}


.btn-primary:focus, .btn-primary.focus
{
    
     box-shadow: none;
}



a.badge-primary:hover, a.badge-primary:focus {
    
    background-color: var(--primary);
}


.btn-link, .btn-link:hover
{
    color: #353535;
    
}



.btn-outline-primary.disabled, .btn-outline-primary:disabled
{
    
    color: #fff;
}

