.container-arc {
  width: 370px;
  height: 200px;
  position: relative;
  margin: auto;
 
  color: #444;
}


.arc-circle {
  stroke: #E0E0E0;
fill: transparent;  
    stroke-width: 12;
}


#percent, #svg {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 0;
  left: 0;

}

#percent {
  line-height: 20px;
  height: 20px;
  /*width: 100%;*/
  top: 90px;
  font-size: 43px;
  text-align: center;
  color: #343a40;
  opacity: 0.8;
}



.container-arc input
{
    border: 0 !important;
  outline: 0 !important;
 
  width: 30px ;
 font-weight: bold;
  font-size: 24px  !important;
  text-transform: capitalise !important;
  
  background: transparent !important;
    
}

/*
