    border: 0.0625rem solid #cccccc;
    max-height: 7.5rem;
    overflow: auto;
    background: white;
    border-radius: 0.25rem;
}

.dropdown-menu.filter .dropdown-item:hover, .dropdown-item:focus
{
    background-color: transparent;
    
}


