}


.dropdown-menu.filter .form-group .input-group-addon {
    display: flex;
    justify-content: center;
    align-items: center;
   
   
    border-radius: 0 0.25rem 0.25rem 0;
    border: 0.0625rem solid #0000001a;
    background: #fdfdfd;
    color: $dark;
}


.dropdown-menu.filter .dropdown-item.active, .dropdown-item:active
{
    color: $dark;
    background: transparent;
    
}

